import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Card, Table, Container, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { fetchUsers, deleteRecord } from "../../../redux/actions";
import { TheHeaderDropdownTasks } from "../../../containers";
import Swal from "sweetalert2";

const Employees = () => {
  const state = useSelector(state => state);
  const dispatch = useDispatch();
  const history = useHistory();
  const users = state.users.users.results;
  const { employee_updated } = state.users;

  const handleRequestDetails = e => {
    e.preventDefault();
    const id = e.target.classList[0];
    return history.push(`/admin/employee/details/${id}`);
  };

  useEffect(() => {
    dispatch(fetchUsers());
  }, [dispatch, employee_updated]);

  const deleteEmployee = e => {
    e.preventDefault();
    const id = e.target.id;

    Swal.fire({
      text: "Confirm Delete? This action is irreversible",
      confirmButtonText: "Confirm",
      showCancelButton: true
    }).then(async ({ value }) => {
      if (value) {
        await dispatch(deleteRecord(id));
        return Swal.fire({
          text: "Delete Successful",
          icon: "success",
          button: false,
          timer: 2000
        });
      } else {
        Swal.fire({
          text: "Goodbye",
          icon: "error",
          timer: 1000
        });
      }
    });
  };

  if (!users) return null;

  return (
    <>
      <h2>Manage Employees</h2>
      <div>
        <div className="pb-5 pt-5 pt-md-8">
          <Container fluid style={{ width: "97%" }}>
            {!users && (
              <div style={{ height: "70vh" }}>
                <div className="bg-white">
                  <div
                    className="d-flex flex-column justify-content-center align-items-center text-dark"
                    style={{ height: "30vh", fontSize: "17px" }}>
                    <p className="pb-0 mb-1 font-weight-bold">There are no registered employees at the moment.</p>
                  </div>
                </div>
              </div>
            )}
            {users.length > 0 && (
              <>
                <Row>
                  <div className="col">
                    <Card className="shadow p-0">
                      <Table className="align-items-center table-flush" responsive>
                        <thead className="thead-light">
                          <tr>
                            <th scope="col">Staff Id</th>
                            <th scope="col">Firstname</th>
                            <th scope="col">Lastname</th>
                            <th scope="col">Middlename</th>
                            <th scope="col">Annual Leave</th>
                            <th scope="col">Sick Leave</th>
                            <th scope="col">Exam Leave</th>
                            <th scope="col">Compass. Leave</th>
                            <th scope="col">Line Manager</th>
                            <th scope="col">Role</th>
                            <th scope="col" />
                          </tr>
                        </thead>

                        <tbody>
                          {users.map((entry, index) => {
                            const {
                              id,
                              firstname,
                              lastname,
                              middlename,
                              staff_id,
                              annual_leave,
                              sick_leave,
                              exam_leave,
                              relieve_staff,
                              compassionate_leave,
                              role,
                              line_manager
                            } = entry;
                            return (
                              <tr key={index}>
                                <td>
                                  <strong>{staff_id}</strong>
                                </td>
                                <td>{firstname}</td>
                                <td>
                                  <div>{lastname}</div>
                                </td>
                                <td>
                                  <div>{middlename}</div>
                                </td>
                                <td>
                                  <div>{annual_leave}</div>
                                </td>
                                <td>
                                  <div>{exam_leave}</div>
                                </td>
                                <td>
                                  <div>{sick_leave}</div>
                                </td>
                                <td>
                                  <div>{compassionate_leave}</div>
                                </td>
                                <td>
                                  <div>{entry.line_manager && `${line_manager.firstname} ${line_manager.lastname}`}</div>
                                </td>
                                <td>
                                  <div>{role}</div>
                                </td>
                                <td className="text-right">
                                  <span>
                                    <TheHeaderDropdownTasks
                                      id={id}
                                      deleteKey={"Delete Record"}
                                      handleDelete={deleteEmployee}
                                      updateKey={"Update Record"}
                                      statusUpdate={() => history.push(`/admin/employees/${id}/update`)}
                                    />
                                  </span>
                                </td>
                              </tr>
                            );
                          })}
                        </tbody>
                      </Table>
                    </Card>
                  </div>
                </Row>
              </>
            )}
          </Container>
        </div>
      </div>
    </>
  );
};

export default Employees;
